import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import axios from 'axios';
import { setAlert } from '../store/actions/alert';

const UploadItinerary = ({ setAlert, isAuthenticated }) => {
  const navigate = useNavigate();
  
  const [formData, setFormData] = useState({
    destinationCountry: '',
    arrivalDate: '',
    departureDate: '',
    flightNumber: '',
    airline: '',
    accommodationAddress: '',
    accommodationPhone: '',
    purpose: 'tourism'
  });
  
  const [file, setFile] = useState(null);
  const [uploading, setUploading] = useState(false);
  
  const {
    destinationCountry,
    arrivalDate,
    departureDate,
    flightNumber,
    airline,
    accommodationAddress,
    accommodationPhone,
    purpose
  } = formData;
  
  const onChange = e => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  
  const onFileChange = e => {
    setFile(e.target.files[0]);
  };
  
  const onSubmit = async e => {
    e.preventDefault();
    
    if (!file) {
      setAlert('Please upload an itinerary file', 'danger');
      return;
    }
    
    // Validate dates
    const arrival = new Date(arrivalDate);
    const departure = new Date(departureDate);
    const now = new Date();
    
    if (arrival < now) {
      setAlert('Arrival date cannot be in the past', 'danger');
      return;
    }
    
    if (departure < arrival) {
      setAlert('Departure date cannot be before arrival date', 'danger');
      return;
    }
    
    const formData = new FormData();
    formData.append('itinerary', file);
    formData.append('destinationCountry', destinationCountry);
    formData.append('arrivalDate', arrivalDate);
    formData.append('departureDate', departureDate);
    formData.append('flightNumber', flightNumber);
    formData.append('airline', airline);
    formData.append('accommodationAddress', accommodationAddress);
    formData.append('accommodationPhone', accommodationPhone);
    formData.append('purpose', purpose);
    
    setUploading(true);
    
    try {
      const res = await axios.post('/api/itineraries', formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });
      
      setAlert('Itinerary uploaded successfully', 'success');
      navigate(`/payment/${res.data.data.submission._id}`);
    } catch (err) {
      const error = err.response.data.error;
      setAlert(error || 'Error uploading itinerary', 'danger');
      setUploading(false);
    }
  };
  
  // Redirect if not authenticated
  if (!isAuthenticated) {
    navigate('/login');
  }
  
  return (
    <UploadContainer>
      <div className="container">
        <h1>Upload Your Travel Itinerary</h1>
        <p className="lead">
          Please provide your travel details and upload your itinerary PDF
        </p>
        
        <form onSubmit={onSubmit}>
          <div className="form-group">
            <label htmlFor="destinationCountry">Destination Country*</label>
            <input
              type="text"
              id="destinationCountry"
              name="destinationCountry"
              value={destinationCountry}
              onChange={onChange}
              required
            />
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label htmlFor="arrivalDate">Arrival Date*</label>
              <input
                type="date"
                id="arrivalDate"
                name="arrivalDate"
                value={arrivalDate}
                onChange={onChange}
                required
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="departureDate">Departure Date*</label>
              <input
                type="date"
                id="departureDate"
                name="departureDate"
                value={departureDate}
                onChange={onChange}
                required
              />
            </div>
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label htmlFor="airline">Airline*</label>
              <input
                type="text"
                id="airline"
                name="airline"
                value={airline}
                onChange={onChange}
                required
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="flightNumber">Flight Number*</label>
              <input
                type="text"
                id="flightNumber"
                name="flightNumber"
                value={flightNumber}
                onChange={onChange}
                required
              />
            </div>
          </div>
          
          <div className="form-group">
            <label htmlFor="accommodationAddress">Accommodation Address*</label>
            <input
              type="text"
              id="accommodationAddress"
              name="accommodationAddress"
              value={accommodationAddress}
              onChange={onChange}
              required
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="accommodationPhone">Accommodation Phone</label>
            <input
              type="text"
              id="accommodationPhone"
              name="accommodationPhone"
              value={accommodationPhone}
              onChange={onChange}
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="purpose">Purpose of Visit*</label>
            <select
              id="purpose"
              name="purpose"
              value={purpose}
              onChange={onChange}
              required
            >
              <option value="tourism">Tourism</option>
              <option value="business">Business</option>
              <option value="education">Education</option>
              <option value="other">Other</option>
            </select>
          </div>
          
          <div className="form-group file-upload">
            <label htmlFor="itinerary">Upload Itinerary (PDF)*</label>
            <input
              type="file"
              id="itinerary"
              name="itinerary"
              onChange={onFileChange}
              accept=".pdf"
              required
            />
            <p className="file-info">
              {file ? `Selected file: ${file.name}` : 'No file selected'}
            </p>
          </div>
          
          <button
            type="submit"
            className="btn btn-primary"
            disabled={uploading}
          >
            {uploading ? 'Uploading...' : 'Continue to Payment'}
          </button>
        </form>
      </div>
    </UploadContainer>
  );
};

UploadItinerary.propTypes = {
  setAlert: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool
};

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

const UploadContainer = styled.div`
  padding: 3rem 0;
  
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    margin-bottom: 1rem;
    text-align: center;
  }
  
  .lead {
    margin-bottom: 2rem;
    text-align: center;
    color: #666;
  }
  
  .form-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
    
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 0;
    }
  }
  
  .form-group {
    margin-bottom: 1.5rem;
  }
  
  label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
  }
  
  input,
  select {
    width: 100%;
    padding: 0.8rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
  }
  
  .file-upload {
    margin-top: 2rem;
  }
  
  .file-info {
    margin-top: 0.5rem;
    font-size: 0.9rem;
    color: #666;
  }
  
  .btn {
    width: 100%;
    padding: 1rem;
    font-size: 1.1rem;
    margin-top: 1rem;
  }
`;

export default connect(mapStateToProps, { setAlert })(UploadItinerary);