import {
  GET_POSTS,
  GET_POST,
  BLOG_ERROR,
  CLEAR_POST
} from '../actions/types';

const initialState = {
  posts: [],
  post: null,
  loading: true,
  error: {}
};

export default function(state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case GET_POSTS:
      return {
        ...state,
        posts: payload,
        loading: false
      };
    case GET_POST:
      return {
        ...state,
        post: payload,
        loading: false
      };
    case BLOG_ERROR:
      return {
        ...state,
        error: payload,
        loading: false
      };
    case CLEAR_POST:
      return {
        ...state,
        post: null,
        loading: false
      };
    default:
      return state;
  }
}