import React, { useState } from 'react';
import { Link, Navigate } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { register } from '../store/actions/auth';
import { setAlert } from '../store/actions/alert';
import styled from 'styled-components';

const Register = ({ register, isAuthenticated }) => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    nationality: '',
    passportNumber: '',
    password: '',
    password2: '',
    preferredNotification: 'email'
  });

  const {
    name,
    email,
    phone,
    nationality,
    passportNumber,
    password,
    password2,
    preferredNotification
  } = formData;

  const onChange = e =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = e => {
    e.preventDefault();
    if (password !== password2) {
      setAlert('Passwords do not match', 'danger');
    } else {
      register({
        name,
        email,
        phone,
        nationality,
        passportNumber,
        password,
        preferredNotification
      });
    }
  };

  if (isAuthenticated) {
    return <Navigate to="/dashboard" />;
  }

  return (
    <RegisterContainer>
      <div className="container">
        <h1>Sign Up</h1>
        <p>Create your account to get started</p>
        <form className="form" onSubmit={onSubmit}>
          <div className="form-group">
            <label htmlFor="name">Full Name</label>
            <input
              type="text"
              id="name"
              name="name"
              value={name}
              onChange={onChange}
              required
            />
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label htmlFor="email">Email Address</label>
              <input
                type="email"
                id="email"
                name="email"
                value={email}
                onChange={onChange}
                required
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="phone">Phone Number (with country code)</label>
              <input
                type="text"
                id="phone"
                name="phone"
                placeholder="+1234567890"
                value={phone}
                onChange={onChange}
                required
              />
            </div>
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label htmlFor="nationality">Nationality</label>
              <input
                type="text"
                id="nationality"
                name="nationality"
                value={nationality}
                onChange={onChange}
                required
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="passportNumber">Passport Number</label>
              <input
                type="text"
                id="passportNumber"
                name="passportNumber"
                value={passportNumber}
                onChange={onChange}
                required
              />
            </div>
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label htmlFor="password">Password</label>
              <input
                type="password"
                id="password"
                name="password"
                value={password}
                onChange={onChange}
                minLength="6"
                required
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="password2">Confirm Password</label>
              <input
                type="password"
                id="password2"
                name="password2"
                value={password2}
                onChange={onChange}
                minLength="6"
                required
              />
            </div>
          </div>
          
          <div className="form-group">
            <label>Preferred Notification Method</label>
            <div className="radio-group">
              <div className="radio-option">
                <input
                  type="radio"
                  id="email-notification"
                  name="preferredNotification"
                  value="email"
                  checked={preferredNotification === 'email'}
                  onChange={onChange}
                />
                <label htmlFor="email-notification">Email</label>
              </div>
              
              <div className="radio-option">
                <input
                  type="radio"
                  id="whatsapp-notification"
                  name="preferredNotification"
                  value="whatsapp"
                  checked={preferredNotification === 'whatsapp'}
                  onChange={onChange}
                />
                <label htmlFor="whatsapp-notification">WhatsApp</label>
              </div>
              
              <div className="radio-option">
                <input
                  type="radio"
                  id="both-notification"
                  name="preferredNotification"
                  value="both"
                  checked={preferredNotification === 'both'}
                  onChange={onChange}
                />
                <label htmlFor="both-notification">Both</label>
              </div>
            </div>
          </div>
          
          <div className="form-group terms">
            <input type="checkbox" id="terms" required />
            <label htmlFor="terms">
              I agree to the <a href="/terms">Terms of Service</a> and <a href="/privacy">Privacy Policy</a>
            </label>
          </div>
          
          <button type="submit" className="btn btn-primary">
            Register
          </button>
        </form>
        <p className="my-1">
          Already have an account? <Link to="/login">Sign In</Link>
        </p>
      </div>
    </RegisterContainer>
  );
};

Register.propTypes = {
  register: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool
};

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

const RegisterContainer = styled.section`
  padding: 3rem 0;
  
  .container {
    max-width: 700px;
    margin: 0 auto;
    padding: 2rem;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
    text-align: center;
  }
  
  p {
    margin-bottom: 1.5rem;
    text-align: center;
  }
  
  .form-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
    
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 0;
    }
  }
  
  .form-group {
    margin-bottom: 1.5rem;
  }
  
  label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
  }
  
  input[type="text"],
  input[type="email"],
  input[type="password"] {
    width: 100%;
    padding: 0.8rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
  }
  
  .radio-group {
    display: flex;
    gap: 2rem;
  }
  
  .radio-option {
    display: flex;
    align-items: center;
  }
  
  .radio-option input {
    margin-right: 0.5rem;
  }
  
  .radio-option label {
    margin: 0;
    font-weight: normal;
  }
  
  .terms {
    display: flex;
    align-items: center;
  }
  
  .terms input {
    margin-right: 0.5rem;
  }
  
  .terms label {
    margin: 0;
    font-weight: normal;
  }
  
  .btn {
    width: 100%;
    padding: 0.8rem;
    font-size: 1rem;
  }
  
  .my-1 {
    margin: 1rem 0;
    text-align: center;
  }
`;

export default connect(mapStateToProps, { register, setAlert })(Register);