import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import axios from 'axios';
import { setAlert } from '../store/actions/alert';

const SubmissionDetail = ({ setAlert, isAuthenticated }) => {
  const { id } = useParams();
  const navigate = useNavigate();
  
  const [submission, setSubmission] = useState(null);
  const [itinerary, setItinerary] = useState(null);
  const [loading, setLoading] = useState(true);
  
  useEffect(() => {
    const fetchSubmission = async () => {
      try {
        const res = await axios.get(`/api/submissions/${id}`);
        setSubmission(res.data.data);
        setItinerary(res.data.data.itinerary);
        setLoading(false);
      } catch (err) {
        setAlert('Error fetching submission details', 'danger');
        navigate('/dashboard');
      }
    };
    
    if (isAuthenticated) {
      fetchSubmission();
    }
  }, [id, isAuthenticated, navigate, setAlert]);
  
  // Redirect if not authenticated
  if (!isAuthenticated) {
    navigate('/login');
    return null;
  }
  
  if (loading) {
    return (
      <SubmissionDetailContainer>
        <div className="container">
          <div className="loading">Loading...</div>
        </div>
      </SubmissionDetailContainer>
    );
  }
  
  const getStatusBadge = (status) => {
    switch (status) {
      case 'pending':
        return <span className="badge badge-warning">Pending</span>;
      case 'scheduled':
        return <span className="badge badge-info">Scheduled</span>;
      case 'processing':
        return <span className="badge badge-primary">Processing</span>;
      case 'submitted':
        return <span className="badge badge-primary">Submitted</span>;
      case 'completed':
        return <span className="badge badge-success">Completed</span>;
      case 'failed':
        return <span className="badge badge-danger">Failed</span>;
      default:
        return <span className="badge badge-secondary">{status}</span>;
    }
  };
  
  const getPaymentStatusBadge = (status) => {
    switch (status) {
      case 'unpaid':
        return <span className="badge badge-danger">Unpaid</span>;
      case 'processing':
        return <span className="badge badge-warning">Processing</span>;
      case 'paid':
        return <span className="badge badge-success">Paid</span>;
      case 'refunded':
        return <span className="badge badge-info">Refunded</span>;
      default:
        return <span className="badge badge-secondary">{status}</span>;
    }
  };
  
  return (
    <SubmissionDetailContainer>
      <div className="container">
        <Link to="/dashboard" className="btn btn-light back-button">
          &larr; Back to Dashboard
        </Link>
        
        <h1>Submission Details</h1>
        
        <div className="submission-status">
          <div className="status-item">
            <span className="status-label">Status:</span>
            <span className="status-value">{getStatusBadge(submission.status)}</span>
          </div>
          
          <div className="status-item">
            <span className="status-label">Payment:</span>
            <span className="status-value">{getPaymentStatusBadge(submission.paymentStatus)}</span>
          </div>
          
          {submission.confirmationNumber && (
            <div className="status-item">
              <span className="status-label">Confirmation #:</span>
              <span className="status-value">{submission.confirmationNumber}</span>
            </div>
          )}
        </div>
        
        {submission.paymentStatus === 'unpaid' && (
          <div className="payment-alert">
            <i className="fas fa-exclamation-circle"></i>
            <p>
              Your submission requires payment before processing.
              <Link to={`/payment/${submission._id}`} className="btn btn-primary">
                Complete Payment
              </Link>
            </p>
          </div>
        )}
        
        {submission.status === 'scheduled' && (
          <div className="info-alert">
            <i className="fas fa-info-circle"></i>
            <p>
              Your arrival card will be submitted on{' '}
              {new Date(itinerary.scheduledSubmissionDate).toLocaleString()}.
            </p>
          </div>
        )}
        
        {submission.status === 'completed' && (
          <div className="success-alert">
            <i className="fas fa-check-circle"></i>
            <p>
              Your arrival card has been successfully submitted. You will receive a
              confirmation via your preferred notification method.
            </p>
          </div>
        )}
        
        <div className="detail-section">
          <h2>Travel Details</h2>
          
          <div className="detail-grid">
            <div className="detail-item">
              <span className="detail-label">Destination:</span>
              <span className="detail-value">{itinerary.destinationCountry}</span>
            </div>
            
            <div className="detail-item">
              <span className="detail-label">Arrival Date:</span>
              <span className="detail-value">
                {new Date(itinerary.arrivalDate).toLocaleString()}
              </span>
            </div>
            
            <div className="detail-item">
              <span className="detail-label">Departure Date:</span>
              <span className="detail-value">
                {new Date(itinerary.departureDate).toLocaleString()}
              </span>
            </div>
            
            <div className="detail-item">
              <span className="detail-label">Airline:</span>
              <span className="detail-value">{itinerary.airline}</span>
            </div>
            
            <div className="detail-item">
              <span className="detail-label">Flight Number:</span>
              <span className="detail-value">{itinerary.flightNumber}</span>
            </div>
            
            <div className="detail-item">
              <span className="detail-label">Purpose:</span>
              <span className="detail-value">{itinerary.purpose}</span>
            </div>
            
            <div className="detail-item full-width">
              <span className="detail-label">Accommodation:</span>
              <span className="detail-value">{itinerary.accommodationAddress}</span>
            </div>
            
            {itinerary.accommodationPhone && (
              <div className="detail-item">
                <span className="detail-label">Accommodation Phone:</span>
                <span className="detail-value">{itinerary.accommodationPhone}</span>
              </div>
            )}
          </div>
        </div>
        
        <div className="detail-section">
          <h2>Submission Timeline</h2>
          
          <div className="timeline">
            <div className="timeline-item active">
              <div className="timeline-marker"></div>
              <div className="timeline-content">
                <h3>Itinerary Uploaded</h3>
                <p>{new Date(itinerary.createdAt).toLocaleString()}</p>
              </div>
            </div>
            
            <div className={`timeline-item ${submission.paymentStatus === 'paid' ? 'active' : ''}`}>
              <div className="timeline-marker"></div>
              <div className="timeline-content">
                <h3>Payment Completed</h3>
                <p>
                  {submission.paymentStatus === 'paid'
                    ? new Date(submission.updatedAt).toLocaleString()
                    : 'Pending'}
                </p>
              </div>
            </div>
            
            <div className={`timeline-item ${submission.status === 'scheduled' ? 'active' : ''}`}>
              <div className="timeline-marker"></div>
              <div className="timeline-content">
                <h3>Scheduled for Submission</h3>
                <p>
                  {submission.status === 'scheduled'
                    ? new Date(itinerary.scheduledSubmissionDate).toLocaleString()
                    : 'Pending'}
                </p>
              </div>
            </div>
            
            <div className={`timeline-item ${submission.status === 'completed' ? 'active' : ''}`}>
              <div className="timeline-marker"></div>
              <div className="timeline-content">
                <h3>Submission Completed</h3>
                <p>
                  {submission.status === 'completed'
                    ? new Date(submission.submissionDate).toLocaleString()
                    : 'Pending'}
                </p>
              </div>
            </div>
          </div>
        </div>
        
        {submission.notes && (
          <div className="detail-section">
            <h2>Notes</h2>
            <p className="notes">{submission.notes}</p>
          </div>
        )}
        
        <div className="action-buttons">
          <Link to="/dashboard" className="btn btn-light">
            Back to Dashboard
          </Link>
          
          {submission.paymentStatus === 'unpaid' && (
            <Link to={`/payment/${submission._id}`} className="btn btn-primary">
              Complete Payment
            </Link>
          )}
          
          {submission.status === 'completed' && submission.arrivalCardPdf && (
            <a
              href={`/api/submissions/${submission._id}/download`}
              className="btn btn-success"
              target="_blank"
              rel="noopener noreferrer"
            >
              Download Arrival Card
            </a>
          )}
        </div>
      </div>
    </SubmissionDetailContainer>
  );
};

SubmissionDetail.propTypes = {
  setAlert: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool
};

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

const SubmissionDetailContainer = styled.div`
  padding: 3rem 0;
  
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  .back-button {
    margin-bottom: 1.5rem;
    display: inline-block;
  }
  
  h1 {
    margin-bottom: 1.5rem;
  }
  
  .loading {
    text-align: center;
    padding: 2rem;
    font-size: 1.2rem;
  }
  
  .submission-status {
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    margin-bottom: 2rem;
    padding: 1rem;
    background-color: #f8f9fa;
    border-radius: 5px;
  }
  
  .status-item {
    display: flex;
    align-items: center;
  }
  
  .status-label {
    font-weight: bold;
    margin-right: 0.5rem;
  }
  
  .payment-alert,
  .info-alert,
  .success-alert {
    display: flex;
    align-items: center;
    padding: 1rem;
    border-radius: 5px;
    margin-bottom: 2rem;
  }
  
  .payment-alert {
    background-color: #fff3cd;
    color: #856404;
  }
  
  .info-alert {
    background-color: #e8f4fd;
    color: #0077b6;
  }
  
  .success-alert {
    background-color: #d4edda;
    color: #155724;
  }
  
  .payment-alert i,
  .info-alert i,
  .success-alert i {
    font-size: 1.5rem;
    margin-right: 1rem;
  }
  
  .payment-alert p,
  .info-alert p,
  .success-alert p {
    flex: 1;
    margin: 0;
  }
  
  .payment-alert .btn {
    margin-left: 1rem;
  }
  
  .detail-section {
    margin-bottom: 2rem;
  }
  
  .detail-section h2 {
    margin-bottom: 1.5rem;
    color: var(--primary-color);
    border-bottom: 1px solid #eee;
    padding-bottom: 0.5rem;
  }
  
  .detail-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
    
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }
  
  .detail-item {
    display: flex;
    flex-direction: column;
  }
  
  .detail-item.full-width {
    grid-column: 1 / -1;
  }
  
  .detail-label {
    font-weight: bold;
    margin-bottom: 0.3rem;
    color: #666;
  }
  
  .detail-value {
    font-size: 1.1rem;
  }
  
  .timeline {
    position: relative;
    padding-left: 2rem;
  }
  
  .timeline::before {
    content: '';
    position: absolute;
    left: 0.5rem;
    top: 0;
    height: 100%;
    width: 2px;
    background-color: #ddd;
  }
  
  .timeline-item {
    position: relative;
    margin-bottom: 2rem;
  }
  
  .timeline-marker {
    position: absolute;
    left: -2rem;
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    background-color: #ddd;
    border: 2px solid #fff;
  }
  
  .timeline-item.active .timeline-marker {
    background-color: var(--primary-color);
  }
  
  .timeline-content h3 {
    margin-bottom: 0.5rem;
    font-size: 1.1rem;
  }
  
  .timeline-content p {
    color: #666;
  }
  
  .notes {
    padding: 1rem;
    background-color: #f8f9fa;
    border-radius: 5px;
    font-style: italic;
  }
  
  .action-buttons {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
    
    @media (max-width: 576px) {
      flex-direction: column;
    }
  }
  
  .badge {
    padding: 0.3rem 0.6rem;
    border-radius: 4px;
    font-size: 0.8rem;
    font-weight: bold;
  }
  
  .badge-primary {
    background-color: var(--primary-color);
    color: #fff;
  }
  
  .badge-success {
    background-color: var(--success-color);
    color: #fff;
  }
  
  .badge-warning {
    background-color: #ffc107;
    color: #212529;
  }
  
  .badge-danger {
    background-color: var(--danger-color);
    color: #fff;
  }
  
  .badge-info {
    background-color: var(--secondary-color);
    color: #fff;
  }
  
  .badge-secondary {
    background-color: #6c757d;
    color: #fff;
  }
`;

export default connect(mapStateToProps, { setAlert })(SubmissionDetail);