import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { updateProfile } from '../store/actions/auth';
import { setAlert } from '../store/actions/alert';

const Profile = ({ auth: { user, loading }, updateProfile, setAlert }) => {
  const navigate = useNavigate();
  
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    nationality: '',
    passportNumber: '',
    preferredNotification: 'email'
  });
  
  const [updating, setUpdating] = useState(false);
  
  useEffect(() => {
    if (user) {
      setFormData({
        name: user.name || '',
        email: user.email || '',
        phone: user.phone || '',
        nationality: user.nationality || '',
        passportNumber: user.passportNumber || '',
        preferredNotification: user.preferredNotification || 'email'
      });
    }
  }, [user]);
  
  const { name, email, phone, nationality, passportNumber, preferredNotification } = formData;
  
  const onChange = e => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  
  const onSubmit = async e => {
    e.preventDefault();
    setUpdating(true);
    
    try {
      await updateProfile(formData);
      setUpdating(false);
    } catch (err) {
      setUpdating(false);
    }
  };
  
  if (loading) {
    return (
      <ProfileContainer>
        <div className="container">
          <div className="loading">Loading...</div>
        </div>
      </ProfileContainer>
    );
  }
  
  return (
    <ProfileContainer>
      <div className="container">
        <h1>Your Profile</h1>
        
        <form onSubmit={onSubmit}>
          <div className="form-group">
            <label htmlFor="name">Full Name</label>
            <input
              type="text"
              id="name"
              name="name"
              value={name}
              onChange={onChange}
              required
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="email">Email Address</label>
            <input
              type="email"
              id="email"
              name="email"
              value={email}
              onChange={onChange}
              required
              disabled
            />
            <small>Email cannot be changed</small>
          </div>
          
          <div className="form-group">
            <label htmlFor="phone">Phone Number (with country code)</label>
            <input
              type="text"
              id="phone"
              name="phone"
              placeholder="+1234567890"
              value={phone}
              onChange={onChange}
              required
            />
          </div>
          
          <div className="form-row">
            <div className="form-group">
              <label htmlFor="nationality">Nationality</label>
              <input
                type="text"
                id="nationality"
                name="nationality"
                value={nationality}
                onChange={onChange}
                required
              />
            </div>
            
            <div className="form-group">
              <label htmlFor="passportNumber">Passport Number</label>
              <input
                type="text"
                id="passportNumber"
                name="passportNumber"
                value={passportNumber}
                onChange={onChange}
                required
              />
            </div>
          </div>
          
          <div className="form-group">
            <label>Preferred Notification Method</label>
            <div className="radio-group">
              <div className="radio-option">
                <input
                  type="radio"
                  id="email-notification"
                  name="preferredNotification"
                  value="email"
                  checked={preferredNotification === 'email'}
                  onChange={onChange}
                />
                <label htmlFor="email-notification">Email</label>
              </div>
              
              <div className="radio-option">
                <input
                  type="radio"
                  id="whatsapp-notification"
                  name="preferredNotification"
                  value="whatsapp"
                  checked={preferredNotification === 'whatsapp'}
                  onChange={onChange}
                />
                <label htmlFor="whatsapp-notification">WhatsApp</label>
              </div>
              
              <div className="radio-option">
                <input
                  type="radio"
                  id="both-notification"
                  name="preferredNotification"
                  value="both"
                  checked={preferredNotification === 'both'}
                  onChange={onChange}
                />
                <label htmlFor="both-notification">Both</label>
              </div>
            </div>
          </div>
          
          <button type="submit" className="btn btn-primary" disabled={updating}>
            {updating ? 'Updating...' : 'Update Profile'}
          </button>
        </form>
      </div>
    </ProfileContainer>
  );
};

Profile.propTypes = {
  auth: PropTypes.object.isRequired,
  updateProfile: PropTypes.func.isRequired,
  setAlert: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth
});

const ProfileContainer = styled.div`
  padding: 3rem 0;
  
  .container {
    max-width: 700px;
    margin: 0 auto;
    padding: 2rem;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    margin-bottom: 2rem;
    text-align: center;
  }
  
  .loading {
    text-align: center;
    padding: 2rem;
    font-size: 1.2rem;
  }
  
  .form-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
    
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 0;
    }
  }
  
  .form-group {
    margin-bottom: 1.5rem;
  }
  
  label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
  }
  
  input[type="text"],
  input[type="email"] {
    width: 100%;
    padding: 0.8rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
  }
  
  input:disabled {
    background-color: #f5f5f5;
    cursor: not-allowed;
  }
  
  small {
    display: block;
    margin-top: 0.3rem;
    color: #666;
  }
  
  .radio-group {
    display: flex;
    gap: 2rem;
  }
  
  .radio-option {
    display: flex;
    align-items: center;
  }
  
  .radio-option input {
    margin-right: 0.5rem;
  }
  
  .radio-option label {
    margin: 0;
    font-weight: normal;
  }
  
  .btn {
    width: 100%;
    padding: 0.8rem;
    font-size: 1rem;
    margin-top: 1rem;
  }
`;

export default connect(mapStateToProps, { updateProfile, setAlert })(Profile);