import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import axios from 'axios';
import { setAlert } from '../store/actions/alert';

const Dashboard = ({ auth: { user }, setAlert }) => {
  const [submissions, setSubmissions] = useState([]);
  const [loading, setLoading] = useState(true);
  
  useEffect(() => {
    const fetchSubmissions = async () => {
      try {
        const res = await axios.get('/api/submissions');
        setSubmissions(res.data.data);
        setLoading(false);
      } catch (err) {
        setAlert('Error fetching submissions', 'danger');
        setLoading(false);
      }
    };
    
    fetchSubmissions();
  }, [setAlert]);
  
  const getStatusBadge = (status) => {
    switch (status) {
      case 'pending':
        return <span className="badge badge-warning">Pending</span>;
      case 'scheduled':
        return <span className="badge badge-info">Scheduled</span>;
      case 'processing':
        return <span className="badge badge-primary">Processing</span>;
      case 'submitted':
        return <span className="badge badge-primary">Submitted</span>;
      case 'completed':
        return <span className="badge badge-success">Completed</span>;
      case 'failed':
        return <span className="badge badge-danger">Failed</span>;
      default:
        return <span className="badge badge-secondary">{status}</span>;
    }
  };
  
  const getPaymentStatusBadge = (status) => {
    switch (status) {
      case 'unpaid':
        return <span className="badge badge-danger">Unpaid</span>;
      case 'processing':
        return <span className="badge badge-warning">Processing</span>;
      case 'paid':
        return <span className="badge badge-success">Paid</span>;
      case 'refunded':
        return <span className="badge badge-info">Refunded</span>;
      default:
        return <span className="badge badge-secondary">{status}</span>;
    }
  };
  
  return (
    <DashboardContainer>
      <div className="container">
        <h1>Dashboard</h1>
        <p className="welcome">Welcome {user && user.name}</p>
        
        <div className="dashboard-actions">
          <Link to="/upload" className="btn btn-primary">
            <i className="fas fa-plus"></i> New Submission
          </Link>
        </div>
        
        <div className="dashboard-content">
          <div className="dashboard-card submissions-card">
            <h2>Your Submissions</h2>
            
            {loading ? (
              <div className="loading">Loading submissions...</div>
            ) : submissions.length === 0 ? (
              <div className="no-submissions">
                <p>You haven't submitted any arrival cards yet.</p>
                <Link to="/upload" className="btn btn-primary">
                  Submit Your First Arrival Card
                </Link>
              </div>
            ) : (
              <div className="submissions-table-container">
                <table className="submissions-table">
                  <thead>
                    <tr>
                      <th>Destination</th>
                      <th>Arrival Date</th>
                      <th>Status</th>
                      <th>Payment</th>
                      <th>Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {submissions.map(submission => (
                      <tr key={submission._id}>
                        <td>{submission.itinerary.destinationCountry}</td>
                        <td>
                          {new Date(submission.itinerary.arrivalDate).toLocaleDateString()}
                        </td>
                        <td>{getStatusBadge(submission.status)}</td>
                        <td>{getPaymentStatusBadge(submission.paymentStatus)}</td>
                        <td>
                          <Link
                            to={`/submissions/${submission._id}`}
                            className="btn btn-sm btn-info"
                          >
                            View
                          </Link>
                          {submission.paymentStatus === 'unpaid' && (
                            <Link
                              to={`/payment/${submission._id}`}
                              className="btn btn-sm btn-success"
                            >
                              Pay
                            </Link>
                          )}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
          </div>
          
          <div className="dashboard-cards">
            <div className="dashboard-card">
              <h3>Profile Information</h3>
              <div className="profile-info">
                <p><strong>Name:</strong> {user && user.name}</p>
                <p><strong>Email:</strong> {user && user.email}</p>
                <p><strong>Phone:</strong> {user && user.phone}</p>
                <p><strong>Nationality:</strong> {user && user.nationality}</p>
              </div>
              <Link to="/profile" className="btn btn-light">
                Edit Profile
              </Link>
            </div>
            
            <div className="dashboard-card">
              <h3>Need Help?</h3>
              <p>
                If you have any questions or need assistance with your submissions,
                our support team is here to help.
              </p>
              <Link to="/contact" className="btn btn-light">
                Contact Support
              </Link>
            </div>
          </div>
        </div>
      </div>
    </DashboardContainer>
  );
};

Dashboard.propTypes = {
  auth: PropTypes.object.isRequired,
  setAlert: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  auth: state.auth
});

const DashboardContainer = styled.div`
  padding: 3rem 0;
  
  h1 {
    margin-bottom: 0.5rem;
  }
  
  .welcome {
    margin-bottom: 2rem;
    font-size: 1.2rem;
  }
  
  .dashboard-actions {
    margin-bottom: 2rem;
  }
  
  .dashboard-content {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
    
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }
  
  .dashboard-cards {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  
  .dashboard-card {
    background: #fff;
    padding: 1.5rem;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    margin-bottom: 2rem;
  }
  
  .dashboard-card h2,
  .dashboard-card h3 {
    margin-bottom: 1.5rem;
    color: var(--primary-color);
    border-bottom: 1px solid #eee;
    padding-bottom: 0.5rem;
  }
  
  .submissions-card {
    overflow: hidden;
  }
  
  .loading,
  .no-submissions {
    padding: 2rem;
    text-align: center;
  }
  
  .no-submissions p {
    margin-bottom: 1.5rem;
  }
  
  .submissions-table-container {
    overflow-x: auto;
  }
  
  .submissions-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .submissions-table th,
  .submissions-table td {
    padding: 1rem;
    text-align: left;
    border-bottom: 1px solid #eee;
  }
  
  .submissions-table th {
    background-color: #f8f9fa;
    font-weight: bold;
  }
  
  .badge {
    padding: 0.3rem 0.6rem;
    border-radius: 4px;
    font-size: 0.8rem;
    font-weight: bold;
  }
  
  .badge-primary {
    background-color: var(--primary-color);
    color: #fff;
  }
  
  .badge-success {
    background-color: var(--success-color);
    color: #fff;
  }
  
  .badge-warning {
    background-color: #ffc107;
    color: #212529;
  }
  
  .badge-danger {
    background-color: var(--danger-color);
    color: #fff;
  }
  
  .badge-info {
    background-color: var(--secondary-color);
    color: #fff;
  }
  
  .badge-secondary {
    background-color: #6c757d;
    color: #fff;
  }
  
  .btn-sm {
    padding: 0.25rem 0.5rem;
    font-size: 0.8rem;
    margin-right: 0.5rem;
  }
  
  .profile-info p {
    margin-bottom: 1rem;
  }
`;

export default connect(mapStateToProps, { setAlert })(Dashboard);