import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import axios from 'axios';
import { setAlert } from '../store/actions/alert';

const Payment = ({ setAlert, isAuthenticated }) => {
  const { id } = useParams();
  const navigate = useNavigate();
  
  const [submission, setSubmission] = useState(null);
  const [itinerary, setItinerary] = useState(null);
  const [loading, setLoading] = useState(true);
  const [processing, setProcessing] = useState(false);
  
  useEffect(() => {
    const fetchSubmission = async () => {
      try {
        const res = await axios.get(`/api/submissions/${id}`);
        setSubmission(res.data.data);
        setItinerary(res.data.data.itinerary);
        setLoading(false);
      } catch (err) {
        setAlert('Error fetching submission details', 'danger');
        navigate('/dashboard');
      }
    };
    
    if (isAuthenticated) {
      fetchSubmission();
    }
  }, [id, isAuthenticated, navigate, setAlert]);
  
  const handlePayment = async () => {
    setProcessing(true);
    
    try {
      const res = await axios.post(`/api/submissions/${id}/payment`);
      setAlert('Payment processed successfully', 'success');
      navigate('/dashboard');
    } catch (err) {
      const error = err.response.data.error;
      setAlert(error || 'Error processing payment', 'danger');
      setProcessing(false);
    }
  };
  
  // Redirect if not authenticated
  if (!isAuthenticated) {
    navigate('/login');
    return null;
  }
  
  if (loading) {
    return (
      <PaymentContainer>
        <div className="container">
          <div className="loading">Loading...</div>
        </div>
      </PaymentContainer>
    );
  }
  
  return (
    <PaymentContainer>
      <div className="container">
        <h1>Complete Your Payment</h1>
        
        <div className="payment-summary">
          <h2>Order Summary</h2>
          
          <div className="summary-item">
            <span>Destination:</span>
            <span>{itinerary.destinationCountry}</span>
          </div>
          
          <div className="summary-item">
            <span>Arrival Date:</span>
            <span>{new Date(itinerary.arrivalDate).toLocaleDateString()}</span>
          </div>
          
          <div className="summary-item">
            <span>Flight:</span>
            <span>{itinerary.airline} {itinerary.flightNumber}</span>
          </div>
          
          <div className="summary-item total">
            <span>Service Fee:</span>
            <span>${submission.amount.toFixed(2)} {submission.currency}</span>
          </div>
        </div>
        
        <div className="payment-details">
          <h2>Payment Details</h2>
          
          <div className="card-info">
            <div className="form-group">
              <label htmlFor="cardNumber">Card Number</label>
              <input
                type="text"
                id="cardNumber"
                placeholder="1234 5678 9012 3456"
                maxLength="19"
              />
            </div>
            
            <div className="form-row">
              <div className="form-group">
                <label htmlFor="expiry">Expiry Date</label>
                <input
                  type="text"
                  id="expiry"
                  placeholder="MM/YY"
                  maxLength="5"
                />
              </div>
              
              <div className="form-group">
                <label htmlFor="cvv">CVV</label>
                <input
                  type="text"
                  id="cvv"
                  placeholder="123"
                  maxLength="3"
                />
              </div>
            </div>
            
            <div className="form-group">
              <label htmlFor="name">Cardholder Name</label>
              <input
                type="text"
                id="name"
                placeholder="John Doe"
              />
            </div>
          </div>
          
          <div className="payment-notice">
            <p>
              <i className="fas fa-info-circle"></i>
              Your payment information is secure and encrypted. We will process your
              arrival card submission within the required 72-hour window before your
              arrival.
            </p>
          </div>
          
          <button
            className="btn btn-primary"
            onClick={handlePayment}
            disabled={processing}
          >
            {processing ? 'Processing...' : `Pay $${submission.amount.toFixed(2)}`}
          </button>
        </div>
      </div>
    </PaymentContainer>
  );
};

Payment.propTypes = {
  setAlert: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool
};

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

const PaymentContainer = styled.div`
  padding: 3rem 0;
  
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 2rem;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  }
  
  h1 {
    margin-bottom: 2rem;
    text-align: center;
  }
  
  .loading {
    text-align: center;
    padding: 2rem;
    font-size: 1.2rem;
  }
  
  .payment-summary {
    margin-bottom: 2rem;
    padding: 1.5rem;
    background-color: #f8f9fa;
    border-radius: 5px;
  }
  
  .payment-summary h2 {
    margin-bottom: 1.5rem;
    font-size: 1.5rem;
  }
  
  .summary-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 1px solid #eee;
  }
  
  .summary-item.total {
    font-weight: bold;
    font-size: 1.2rem;
    border-top: 2px solid #ddd;
    border-bottom: none;
    padding-top: 1rem;
  }
  
  .payment-details h2 {
    margin-bottom: 1.5rem;
    font-size: 1.5rem;
  }
  
  .card-info {
    margin-bottom: 2rem;
  }
  
  .form-row {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
    
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 0;
    }
  }
  
  .form-group {
    margin-bottom: 1.5rem;
  }
  
  label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
  }
  
  input {
    width: 100%;
    padding: 0.8rem;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 1rem;
  }
  
  .payment-notice {
    margin-bottom: 2rem;
    padding: 1rem;
    background-color: #e8f4fd;
    border-radius: 5px;
    color: #0077b6;
  }
  
  .payment-notice i {
    margin-right: 0.5rem;
  }
  
  .btn {
    width: 100%;
    padding: 1rem;
    font-size: 1.1rem;
  }
`;

export default connect(mapStateToProps, { setAlert })(Payment);